.modal {
  box-sizing: border-box;
  width: 335px;
  z-index: 30;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  margin-bottom: 0;
  padding: 40px 24px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

::-webkit-scrollbar {
  width: 0;
}

.modal-close {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal-icon {
  stroke: #111;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-content {
  flex-direction: column;
  display: flex;
}

.modal-picture {
  width: 285px;
  height: 408px;
  border: none;
  margin-bottom: 14px;
  padding: 0;
}

.modal-picture-content {
  object-fit: contain;
  width: 285px;
  height: 408px;
  border-radius: 8px;
  margin: 0;
  padding: 0;
}

.modal-info {
  margin-bottom: 44px;
}

.modal-title {
  margin-bottom: 4px;
  font-size: 18px;
}

.modal-author {
  color: #b4afaf;
  margin-top: 0;
  margin-bottom: 14px;
  font-size: 12px;
  font-style: italic;
}

.modal-description {
  font-size: 18px;
}

.modal-icons {
  gap: 20px;
  display: flex;
}

.modal-btn {
  height: 46px;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  margin: auto auto 0;
  padding: 14px 28px;
  font-size: 14px;
  display: block;
}

